.field {
    display: flex;
    flex-direction: column;
}

.superInput {
    max-width: 250px;
    width: 100%;
    height: 46px;
    margin: 0;
    padding: 0 10px;
    border: 2px solid #656565;
    border-radius: 5px;
    outline: none;
    box-sizing: border-box;
    font-size: 20px;
    transition: border-color 0.3s ease;
}

.superInput:hover,
.superInput:focus {
    border: 2px solid #000000;
}

.errorInput {
    border-color: #d01d22;
}

.errorInput:hover,
.errorInput:focus {
    border-color: #d01d22;
}

.error {
    font-size: 16px;
    font-weight: 500;
    color: #d01d22;
}